@charset "UTF-8";
@font-face {
  font-family: "PTMono";
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "RobotoCondensed";
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "RobotoCondensed";
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.html {
  height: 100%;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16.41px;
  font-weight: 400;
  font-style: normal;
  scroll-behavior: smooth;
  margin: 0 auto;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.body {
  height: 100%;
  display: flex;
  margin: 0;
}

.main {
  flex: 1 0 auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  padding: 0;
  margin: -1px;
}

a {
  text-decoration: none;
  color: rgb(0, 0, 0);
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

.preloader {
  position: fixed;
  inset: 0;
  z-index: 1001;
}

.preloader__container {
  position: relative;
  top: 50%;
  left: 50%;
  width: 70px;
  height: 70px;
  margin-top: -35px;
  margin-left: -35px;
  text-align: center;
  animation: preloader-rotate 2s infinite linear;
}

.preloader__item {
  position: absolute;
  display: inline-block;
  top: 0;
  background-color: skyblue;
  border-radius: 100%;
  width: 35px;
  height: 35px;
  animation: preloader-bounce 2s infinite ease-in-out;
}

.preloader__item:last-child {
  top: auto;
  bottom: 0;
  animation-delay: -1s;
}

@keyframes preloader-rotate {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes preloader-bounce {
  0%, 100% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
}
:root {
  --calculation-of-one-pixel: calc(1vw + 1vh);
  --pixel: calc(var(--calculation-of-one-pixel) * 0.041789);
}

html,
body,
.page {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  transition: all 0.8s cubic-bezier(0.54, 0.35, 0.29, 0.99);
  -webkit-transition: all 0.8s cubic-bezier(0.54, 0.35, 0.29, 0.99);
  -moz-transition: all 0.8s cubic-bezier(0.54, 0.35, 0.29, 0.99);
  -o-transition: all 0.8s cubic-bezier(0.54, 0.35, 0.29, 0.99);
  color: #fff;
  overflow: hidden;
}

* {
  font-family: Georgia, "Times New Roman", Times, serif;
}

.page {
  position: absolute;
  padding-top: calc(var(--pixel) * 30);
  padding-left: calc(var(--pixel) * 30);
}

#p1 {
  left: 0;
}

#p2,
#p3,
#p4,
#p5 {
  left: 200%;
}

#p1 {
  background: #3d769b;
}

#p2 {
  background: linear-gradient(#00aca9, #239694, #477473, #353535);
}

#p3 {
  background: linear-gradient(#00aca9, #008582, #353535);
}

#p4 {
  background: linear-gradient(#00aca9, #008582, #353535);
}

#p5 {
  background: linear-gradient(#00aca9, #008582, #353535);
}

#t2:target #p2,
#t3:target #p3,
#t4:target #p4,
#t5:target #p5 {
  transform: translateX(-190%);
  -webkit-transform: translateX(-190%);
  -moz-transform: translateX(-190%);
  -o-transform: translateX(-190%);
  transition-delay: 0.4s !important;
}

#t2:target #p1,
#t3:target #p1,
#t4:target #p1,
#t5:target #p1 {
  background: black;
}

#t2:target #p1 .menu__icon,
#t3:target #p1 .menu__icon,
#t4:target #p1 .menu__icon,
#t5:target #p1 .menu__icon {
  -webkit-filter: blur(calc(var(--pixel) * 3));
}

.menu__icon {
  color: #fff;
  font-size: calc(var(--pixel) * 23);
  display: block;
}

ul .menu__icon:hover {
  opacity: 0.5;
}

.page .menu__icon .title {
  line-height: calc(var(--pixel) * 50);
}

#t2:target ul .menu__icon,
#t3:target ul .menu__icon,
#t4:target ul .menu__icon,
#t5:target ul .menu__icon {
  transform: scale(0.6);
  -webkit-transform: scale(0.6);
  -moz-transform: scale(0.6);
  -o-transform: scale(0.6);
  transition-delay: 0.25s;
}

#t2:target #dos,
#t3:target #tres,
#t4:target #cuatro,
#t4:target #cinco {
  transform: scale(1.2) !important;
  -webkit-transform: scale(1.2) !important;
  -moz-transform: scale(1.2) !important;
  -o-transform: scale(1.2) !important;
}

#menu {
  position: fixed;
  z-index: 1;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  height: calc(var(--pixel) * 280);
  width: 10%;
  padding: 0;
  text-align: center;
}

#menu .menu__icon {
  margin: calc(var(--pixel) * 30) 0;
  transition: all 0.5s ease-out !important;
  -webkit-transition: all 0.5s ease-out;
  -moz-transition: all 0.5s ease-out;
  -o-transition: all 0.5s ease-out;
}

a {
  text-decoration: none;
}

.title,
.hint {
  display: block;
  color: black;
}

.title {
  font-size: calc(var(--pixel) * 36);
  width: calc(var(--pixel) * 500);
  margin: 0;
  padding: 0;
}

.hint {
  font-size: calc(var(--pixel) * 22);
  width: calc(var(--pixel) * 500);
}

#p4 .hint {
  display: inherit !important;
}

.hint a {
  color: yellow;
  transition: all 250ms ease-out;
  -webkit-transition: all 250ms ease-out;
  -moz-transition: all 250ms ease-out;
  -o-transition: all 250ms ease-out;
}

.hint a:hover {
  color: #FFF;
}

.line-trough {
  text-decoration: line-through;
}

.page .menu__icon {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 10%;
  left: 0;
  width: calc(var(--pixel) * 270);
  height: calc(var(--pixel) * 170);
  margin: auto;
  text-align: center;
  font-size: calc(var(--pixel) * 80);
  line-height: calc(var(--pixel) * 1.3);
  transform: translateX(360%);
  -webkit-transform: translateX(360%);
  -moz-transform: translateX(360%);
  -o-transform: translateX(360%);
  transition: all 0.5s cubic-bezier(0.25, 1, 0.5, 1.25);
  -webkit-transition: all 0.5s cubic-bezier(0.25, 1, 0.5, 1.25);
  -moz-transition: all 0.5s cubic-bezier(0.25, 1, 0.5, 1.25);
  -o-transition: all 0.5s cubic-bezier(0.25, 1, 0.5, 1.25);
}

.page#p1 .menu__icon {
  height: calc(var(--pixel) * 220);
}

.page#p1 .menu__icon {
  transform: translateX(10%) !important;
}

#t2:target .page#p2 .menu__icon,
#t3:target .page#p3 .menu__icon,
#t4:target .page#p4 .menu__icon,
#t5:target .page#p5 .menu__icon {
  transform: translateX(0) !important;
  -webkit-transform: translateX(0) !important;
  -moz-transform: translateX(0) !important;
  -o-transform: translateX(0) !important;
  transition-delay: 1s;
}

.archive__opened-document-of-button-create {
  font-size: calc(var(--pixel) * 15);
  width: calc(var(--pixel) * 150);
  height: calc(var(--pixel) * 40);
  padding: calc(var(--pixel) * 5);
  margin: calc(var(--pixel) * 5) 0;
  border: calc(var(--pixel) * 1) solid black;
  background-color: transparent;
  color: white;
}
.archive__opened-document-of-button-create:hover {
  transition: 0.3s;
  background-color: black;
  color: white;
  cursor: pointer;
}
.archive__opened-document-of-button-create:not(:hover) {
  transition: 0.3s;
  background-color: white;
  color: black;
  cursor: pointer;
}

.archive__opened-document-of-input {
  font-size: calc(var(--pixel) * 20);
  width: calc(var(--pixel) * 200);
  height: calc(var(--pixel) * 30);
  margin: 0 calc(var(--pixel) * 10);
  border: calc(var(--pixel) * 1) solid black;
}

.archive {
  position: absolute;
}

.archive__filder-menu-container-img {
  position: relative;
  display: block;
}

.archive__filder-menu-img {
  position: absolute;
  width: calc(var(--pixel) * 25);
  height: calc(var(--pixel) * 25);
  top: calc(var(--pixel) * 10);
  left: calc(var(--pixel) * -3);
  rotate: 90deg;
  animation: horizontal-shaking 0.35s infinite;
  cursor: pointer;
}

.archive__filder-menu-img--rotate {
  width: calc(var(--pixel) * 25);
  height: calc(var(--pixel) * 25);
  margin: calc(var(--pixel) * 10) calc(var(--pixel) * 5);
  rotate: -90deg;
  cursor: pointer;
}

.archive__filder-title {
  position: absolute;
  top: calc(var(--pixel) * 15);
  left: calc(var(--pixel) * 100);
  color: #000000;
}

.archive__folder-input {
  font-size: calc(var(--pixel) * 15);
  width: calc(var(--pixel) * 230);
  height: calc(var(--pixel) * 25);
  border: calc(var(--pixel) * 1) solid black;
  margin: calc(var(--pixel) * 10) 0;
}

.archive__folder-conteiner-checkbox-type-of-search {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: calc(var(--pixel) * 20);
  width: calc(var(--pixel) * 65);
}

.archive__folder-input-type-of-search {
  z-index: 99;
  position: absolute;
  width: calc(var(--pixel) * 20);
  height: calc(var(--pixel) * 20);
  visibility: hidden;
}

#type-folder-search-of-number {
  left: 0;
}

#type-folder-search-of-name {
  right: 0;
}

.archive__folder-icon-of-checkbox {
  width: calc(var(--pixel) * 20);
  height: calc(var(--pixel) * 20);
  cursor: pointer;
}

.archive__folder {
  z-index: 99;
  display: none;
  position: absolute;
  font-size: calc(var(--pixel) * 15);
  top: calc(var(--pixel) * 0);
  left: calc(var(--pixel) * 0);
  width: calc(var(--pixel) * 400);
  height: 100%;
  background-color: #FFF;
}

.archive__folder-content-container table thead tr th:nth-child(1) {
  border: none !important;
}

.archive__folder-menu-form {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  max-width: calc(var(--pixel) * 400);
}

.archive__folder-button {
  width: calc(var(--pixel) * 20);
  height: calc(var(--pixel) * 20);
  margin: calc(var(--pixel) * 10);
  cursor: pointer;
}

.archive__folder-content-container {
  overflow-y: scroll;
  height: calc(var(--pixel) * 800);
}

.authorization {
  display: flex;
  justify-content: center;
  text-align: center;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.842);
}

.authorization h1 {
  z-index: 5;
  position: absolute;
  font-family: "Ubuntu", sans-serif;
  font-size: 48px;
  font-size: 4.8rem;
  font-weight: bold;
  top: 50px;
  right: 50px;
  color: #01d1ce;
  text-decoration: underline;
  text-shadow: 0 0 10px #000, 0 0 20px #000, 0 0 30px #000, 0 0 40px #000, 0 0 50px #000, 0 0 60px #000, 0 0 70px #000;
  animation: text-glow 2s ease-out infinite alternate;
  user-select: none;
}

.authorization__svg-doc {
  position: absolute;
  width: 170px;
  height: 120px;
  bottom: 25px;
  left: 25px;
}

.authorization__container-background-img {
  position: absolute;
  width: 650px;
  height: 650px;
  top: 150px;
}

.authorization__svg-circle {
  width: 100%;
  height: 100%;
  animation-name: circle-rotate;
  animation-iteration-count: infinite;
  animation-duration: 12s;
  animation-timing-function: linear;
}

.authorization__svg-circle-two {
  position: absolute;
  top: 30px;
  right: 30px;
  width: 90%;
  height: 90%;
  animation-name: circle-rotate;
  animation-iteration-count: infinite;
  animation-duration: 8s;
  animation-timing-function: linear;
}

.authorization__svg-circle-three {
  position: absolute;
  top: -47px;
  right: -46px;
  width: 115%;
  height: 115%;
  animation-name: circle-rotate;
  animation-iteration-count: infinite;
  animation-duration: 19s;
  animation-timing-function: linear;
}

@keyframes circle-rotate {
  from {
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
.authorization__svg-egg {
  border: 5px solid rgba(1, 209, 206, 0.7882352941);
  border-radius: 50%;
  position: absolute;
  top: -15px;
  right: -15px;
  width: 105%;
  height: 105%;
}

.authorization__form {
  z-index: 4;
  position: relative;
  display: flex;
  justify-content: center;
  text-align: center;
  flex-direction: column;
  height: 260px;
  width: 660px;
  margin: 250px auto 0;
  background: #111;
  background: linear-gradient(#004746, #111111);
  border: 6px solid #00fcf7;
  border-radius: 5px;
  box-shadow: 0 0 15px #00fffd;
}

.authorization__input {
  background: #222;
  background: linear-gradient(#333333, #222222);
  border: 1px solid #444;
  border-radius: 5px;
  box-shadow: 0 2px 0 #000;
  color: #888;
  display: block;
  font-family: "Cabin", helvetica, arial, sans-serif;
  font-size: 13px;
  font-size: 1.3rem;
  height: 40px;
  margin: 20px auto 10px;
  padding: 0 10px;
  text-shadow: 0 -1px 0 #000;
  width: 400px;
}

.authorization__button {
  animation: input-entry 3s ease-in;
  background: #222;
  background: linear-gradient(#333333, #222222);
  box-sizing: content-box;
  border: 1px solid #444;
  border-left-color: #000;
  border-radius: 5px;
  box-shadow: 0 2px 0 #000;
  color: #fff;
  display: block;
  font-family: "Cabin", helvetica, arial, sans-serif;
  font-size: 13px;
  font-weight: 400;
  height: 40px;
  line-height: 40px;
  margin: 20px auto;
  padding: 0;
  position: relative;
  text-shadow: 0 -1px 0 #000;
  width: 400px;
  transition: 1s all;
}
.authorization__button:hover {
  border-color: #00fcf7;
  cursor: pointer;
}

.administration {
  position: relative;
  height: 100%;
}

.administration__title {
  position: absolute;
  height: 30px;
  visibility: hidden;
  user-select: none;
}

.page__admin-button-open-menu {
  position: absolute;
