  height: calc(var(--pixel) * 25);
  top: calc(var(--pixel) * 10);
  left: calc(var(--pixel) * 0);
  rotate: 90deg;
  cursor: pointer;
}

.page__admin-button-open-menu--cliked {
  rotate: 270deg;
}

.page__admin-menu {
  display: none;
