  overflow-y: scroll;
}
.employees-company__users > tbody > tr {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.employees-company__users > tbody > tr > td {
  height: 40px;
  cursor: pointer;
}
.employees-company__users > tbody > tr > td:hover {
  background-color: cadetblue;
}
.employees-company__users > thead {
  font-size: larger;
  background-color: rgb(101, 105, 105);
}
.employees-company__users > thead > tr {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.group-access__title {
  padding: 10px;
  background-color: #008582;
}

.group-access__content-wrapper {
