}

.protocols__navigations-list {
  display: flex;
  list-style-type: none;
}

.archived-protocols__navigations-list {
  display: flex;
  list-style-type: none;
}

.archived-protocols__table {
  display: block;
  width: 1024px;
  height: 500px;
